// Grid
$grid-gutter-width: 15px;
$grid-gutter-width-lg: 30px;

$grid-breakpoints: (
  xs: 0,
  sm: 375px,
  md: 768px,
  lg: 992px,
  xl: 1260px,
  xxl: 1560px,
);

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1200px,
  xxl: 1500px,
);

$spacer: calc-em(20, $unit: 1rem);
$spacers: (
  0: 0,
  1: $spacer * 0.5,   // 10
  2: $spacer * 1,     // 20
  3: $spacer * 1.5,   // 30
  4: $spacer * 2,     // 40
  g: $grid-gutter-width,
);

// Typography
$font-family-base: "sofia-pro", "Helvetica Neue", Arial, sans-serif;

$line-height-base: calc-em(20, $context: 16, $unit: 1);
$line-height-lg: $line-height-base;
$line-height-sm: calc-em(17, $context: 14, $unit: 1);

$headings-font-weight: 700;
$headings-line-height: $line-height-base;

$font-size-base: calc-em(16);
$font-size-xs: calc-em(12);
$h1-font-size: calc-em(32);
$h2-font-size: calc-em(24);
$h3-font-size: calc-em(16);
$h4-font-size: calc-em(16);

$h1-font-size-lg: calc-em(32);
$h2-font-size-lg: calc-em(20);
$h3-font-size-lg: calc-em(16);
$h4-font-size-lg: calc-em(20);

$hero-banner-font-size: calc-em(32);
$hero-banner-font-size-lg: calc-em(72);
$playground-font-size: calc-em(56);
$enable-dark-mode: false;
// Colors
$primary: #D62E55;
$primary-light: #F5DADF;
$primary-light-second: #FFF2F4;
$primary-dark: #B22D4B;
$secondary: #00635B;
$secondary-light: #E0F0E9;
$sh-purple: #993366;
$sh-purple-light: #FFEFFF;
$bali-bod-blue: #213E7B;
$bali-bod-blue-light: #bec5d8;
$sh-orange: #FF9900;
$sh-orange-light: #FCF6E4;
$green-light: #00B67A;
$yellow-light: #f9eeca;
$yellow-light-secondary: #FDF8EA;
$pink-light: #F9F8F8;
$survey-yellow: #FBE598;

$body-color: #151515;

$gray-100: #F3F3F3;
$gray-400: #D0D0D0;
$gray-500: #A3A3A3;
$gray-600: #4E4E4E;
$gray-700: #7B7B7B;
$gray-800: #ADADAD;

$light: #CECECE;
$light-gray: #f5f5f5;
$black: $body-color;
$text-muted: $gray-500;
$border-color: $gray-400;

$theme-colors: (
  'primary': $primary,
  'primary-light': $primary-light,
  'primary-light-second': $primary-light-second,
  'primary-dark': $primary-dark,
  'secondary': $secondary,
  'secondary-light': $secondary-light,
  'sh-purple': $sh-purple,
  'sh-purple-light': $sh-purple-light,
  'bali-bod-blue': $bali-bod-blue,
  'bali-bod-blue-light': $bali-bod-blue-light,
  'sh-orange': $sh-orange,
  'sh-orange-light': $sh-orange-light,
  'green-light': $green-light,
  'yellow-light': $yellow-light,
  'yellow-light-secondary': $yellow-light-secondary,
  'pink-light': $pink-light,
  'dark': $black,
  'gray-100': $gray-100,
  'gray-600': $gray-600,
  'gray-800': $gray-800,
  'light': $light,
  'light-gray': $light-gray,
  'survey-yellow': $survey-yellow,
);

$shade-colors: (
  'dark': #885757,
  'ultra-dark': #5C2323,
  'medium': #AA7878
);

// Border
$border-radius-lg: calc-em(8, $unit: 1rem);

// Forms
$form-group-margin-bottom: map-get($spacers, 1);
$input-color: $body-color;
$input-placeholder-color: $body-color;
$input-btn-focus-color: transparent;
$input-focus-border-color: $gray-400;
$input-disabled-bg: $gray-100;
$input-disabled-color: $gray-400;
$custom-control-gutter: .5em;
$custom-control-indicator-size: calc-em(20);

$input-padding-y: calc-em(28 / 2);
$input-padding-x: 1em;
$input-height: calc-em(50, $unit: 1rem);

$input-padding-y-sm: calc-em(15 / 2, 14);
$input-padding-x-sm: calc-em(6, 14);

$search-icon-padding-y-sm: calc-em(6, 14);
$input-border-radius-sm: calc-em(18, 14);

// Nav
$nav-link-padding-y: calc-em(12/2);
$nav-link-padding-x: calc-em(12/2);
$nav-link-padding-y-lg: calc-em(24/2);
$nav-link-padding-x-lg: calc-em(24/2);

// Buttons
$btn-hover-darken: 23.5%;
$btn-font-weight: 700;
$btn-padding-y: calc-em(9);
$btn-padding-x: calc-em(28);
$btn-padding-x-icon: calc-em(20);

$btn-font-size-lg: 1em;
$btn-line-height-lg: $line-height-base;
$btn-border-radius-lg: $border-radius-lg;
$btn-border-width-lg: 2px;
$btn-padding-y-lg: calc-em(26 / 2);
$btn-padding-x-lg: calc-em(54);

$btn-padding-y-sm: calc-em(15 / 2, 14);
$btn-padding-x-sm: calc-em(28, 14);

// HR
$hr-border-color: $gray-600;
$hr-opacity: 1;

// Carousels
$carousel-indicator-width: calc-em(12);
$carousel-indicator-height: calc-em(12);
$carousel-control-color: $primary;
$carousel-control-prev-icon-bg: none;
$carousel-control-next-icon-bg: none;
$carousel-indicator-active-bg: white;
$carousel-control-width: 10%;
$carousel-indicator-opacity: .7;
$carousel-indicator-active-opacity: 1;

// Upsell
$upsell-padding-y: calc-em(10);
$upsell-padding-x: calc-em(15);
$upsell-padding-y-lg: calc-em(10);
$upsell-padding-x-lg: calc-em(10);

// Video Carousel
$carousel-control-opacity: 1;
$carousel-control-hover-opacity: 1;

// Breadcrumb
$breadcrumb-bg: white;
$breadcrumb-divider-color: $body-color;
$breadcrumb-active-color: $body-color;
$breadcrumb-divider: quote(">");
$breadcrumb-item-padding: calc-em(5);
$breadcrumb-font-size: calc-em(12);
$breadcrumb-padding-x: 0;

// Social Icon
$icon-primary-font-size: calc-em(30);

// Alert
$alert-padding-x: 1.25em;

// Interactions
$hover-transition-timing: 0.15s ease-in-out;

// accordion
$card-border-radius: 0;
$card-border-color: $gray-500;
$card-cap-bg: transparent;
$accordion-button-active-bg: transparent;
$accordion-button-active-color: $black;
$accordion-border-color: $black;
$accordion-button-focus-border-color: $black;

// navbar
$navbar-nav-link-padding-x: 0;
$navbar-padding-y: 0;
$navbar-padding-x: calc-em(15 / 2, 16);
$navbar-toggler-padding-x: 0;

// navbar brand
$navbar-brand-padding-y: calc-em(9, 16);

// Icons
$supernova-icon-asset: url(font-url('supernova-icon.woff2')) format("woff2"),
url(font-url('supernova-icon.woff')) format("woff");

// Select
$custom-select-indicator-padding: 1em;
$custom-select-indicator: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 22.6 22.6'><path d='M1.9 5.4l9.4 9.4 9.4-9.4 1.9 1.9-11.3 11.3L0 7.3l1.9-1.9z'/></svg>");
$custom-select-bg-size: calc-em(16) calc-em(9);
$custom-select-disabled-bg: $gray-100;
$custom-select-disabled-color: $gray-400;
$custom-select-background-mask: $custom-select-indicator no-repeat right $input-padding-x/3 center / $custom-select-bg-size !default;

// Badge
$badge-font-size: 1em;
$badge-padding-y: calc-em(2);
$badge-padding-x: calc-em(12);
$badge-border-radius: calc-em(8);
$badge-circle-width: calc-em(66);
$badge-circle-height: calc-em(66);

// Nav
$nav-link-color: $body-color;

// Nav Tabs
$nav-tabs-link-active-bg: white;
$nav-tabs-link-active-color: $primary;
$nav-tabs-border-width: 0;
$nav-tabs-border-radius: 6px;
$nav-tabs-border-color: black;
$nav-tabs-link-hover-border-color: $body-color;
$nav-tabs-link-active-border-color: $body-color;

//modal
$modal-lg: calc-em(720);

// Custom switch
$custom-switch-indicator-size-height: calc-em(42);
$custom-switch-width: calc-em(82);
$custom-switch-indicator-border-radius: 2em;
$custom-control-indicator-bg: white;
$custom-control-indicator-border-color: white;
$custom-control-indicator-checked-disabled-bg: $gray-400;

$enable-negative-margins: true;

//Progress
$progress-height: calc-em(9, $unit: 1rem);

//Input Range
$form-range-track-bg: $primary-light-second;
$form-range-track-height: calc-em(9, $unit: 1rem);
